import { Component, OnInit, ViewChild } from '@angular/core';

import { FormBuilder,FormGroup,Validators} from '@angular/forms';
import {Feedback,ContactType} from '../shared/feedback';
import {FEEDBACK} from '../shared/feedbacks';
import {visibility,flyinout,expand} from '../animation/app.animation';
import {FeedbackService} from '../services/feedback.service';


@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss'],
  host: {
    '[@flyinout]': 'true',
    'style': 'display: block;'
    },
    animations: [
      visibility(),
      flyinout(),
      expand()
    ]
})
export class ContactComponent implements OnInit {

  feedbackForm:FormGroup;
  feedbackForm2:FormGroup;
  feedback:Feedback;
  contacttype= ContactType;
  errMsg:String;
  feed:Feedback[]=[];
  visibility='shown';
   cc:Feedback;

  formErrors={
    'firstname':'',
    'lastname':'',
    'telnum':'',
    'email':''
  };

  validationMessages={
    'firstname':{
      'required':'First name is required',
      'minlength':'First name must be at least two character',
      'maxlength':'first name cannot be more thsn 20 character'
    },
    'lastname':{
      'required':'Last name is required',
      'minlength':'Last name must be at least two character',
      'maxlength':'Last name cannot be more thsn 20 character'

    },
    'telnum':{
      'required':'telnum is required',
      'pattern':'telnum must contain only number'
    },
    'email':{
      'required':'email is required',
      'email':'Email not in valid format'
    }
  };

  constructor(private fb:FormBuilder,private feedbackService:FeedbackService) {
    this.createForm();
   }

  ngOnInit() {
  }

  createForm(){
    this.feedbackForm=this.fb.group({
      firstname:['',[Validators.required,Validators.minLength(2),Validators.maxLength(20)]],
      lastname: ['',[Validators.required,Validators.minLength(2),Validators.maxLength(20)]],
      telnum:[0,[Validators.required,Validators.pattern]],
      email:['',[Validators.required,Validators.email]],
      agree:false,
      contacttype:'None',
      message: ''
    });
    
    // this.onValueChanged();

    this.feedbackForm.valueChanges
    .subscribe(data=>{
      this.onValueChanged(data)});

    

    
  }
  onValueChanged(data?){
    if(!this.feedbackForm){return;}
    const form=this.feedbackForm;
    for(const field in this.formErrors){
      if(this.formErrors.hasOwnProperty(field))
      {
        this.formErrors[field]='';
        const control=form.get(field);
        if(control && control.dirty && !control.valid){
          const messages=this.validationMessages[field];
          for (const key in control.errors){
            if(control.errors.hasOwnProperty(key))
            {
              this.formErrors[field]+=messages[key]+' ';
            }
          }
        }
      }
    }

  }
  onSubmit(){
    const cc=<Feedback>this.feedbackForm.value;
    console.log();
    this.feedbackService.getfeedback().subscribe(feedb=>this.feed=feedb,errmsg=>this.errMsg=errmsg);
    
    this.feed.push(cc);
    console.log(this.feed);
    this.feedbackForm2=this.feedbackForm;
    
    const t=(this.feedbackService.submitFeedback(this.feed));
    this.visibility='hidden';
    t.subscribe(feedback=>{this.feed=feedback,this.visibility='display'},errmsg=>this.errMsg=errmsg);
    setTimeout(()=>this.visibility='shown',5000);
    setTimeout(()=>this.feedbackForm.reset({
      firstname:'',
      lastname:'',
      telnum:0,
      email:'',
      agree:false,
      contacttype:'None',
      message:''
    }),5000);
    
  }


}
