import { Component, OnInit ,Inject} from '@angular/core';

import {Dish} from '../shared/dish';
import {DishService} from '../services/dish.service';

import {Promotion} from '../shared/promotion';
import {PromotionService} from '../services/promotion.service';

import {Leader} from '../shared/leader';
import {LeadersService} from '../services/leaders.service';
import {flyinout,expand} from '../animation/app.animation';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  host: {
    '[@flyinout]': 'true',
    'style': 'display: block;'
    },
    animations: [
      flyinout(),expand()
    ]
})
export class HomeComponent implements OnInit {

  dish:Dish;
  promotion:Promotion;
  leader:Leader;
  disherrMsg:String;
  promoerrMsg:String;
  leadererrMsg:String;


  constructor(private leaderservice:LeadersService,private dishservice :DishService,private promotionservice:PromotionService,
    @Inject('BaseURL') private BaseURL) { }

  ngOnInit() {

    this.dishservice.getFeaturedDish()
    .subscribe((dish)=> this.dish=dish,errmsg=>this.disherrMsg=errmsg);

    this.leaderservice.getFeaturedLeader()
    .subscribe((Leader)=>this.leader=Leader,errmsg=>this.leadererrMsg=errmsg);

    this.promotionservice.getFeaturedPromotion()
    .subscribe((promotion)=>this.promotion=promotion,errmsg=>this.promoerrMsg=errmsg);
  }

}
