import { Component, OnInit,Inject } from '@angular/core';
import {Dish} from '../shared/dish';


import { FormBuilder,FormGroup,Validators} from '@angular/forms';
import { DishService } from '../services/dish.service';

import { Params, ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {switchMap} from 'rxjs/operators';

import {ProcessHttpMsgService} from '../services/process-http-msg.service';

import {Comment} from '../shared/comment';
import {trigger,style,animate,transition,state} from '@angular/animations';
import {visibility,flyinout,expand} from '../animation/app.animation';

@Component({
  selector: 'app-dishdetail',
  templateUrl: './dishdetail.component.html',
  styleUrls: ['./dishdetail.component.scss'],
  host: {
    '[@flyinout]': 'true',
    'style': 'display: block;'
    },
    animations: [
      flyinout(),
      expand(),
      visibility()
    ]
})
export class DishdetailComponent implements OnInit {

  
  dish:Dish;
  dishIds: string[];
  prev:string;
  next:string;
  errMsg:string;
  dishcopy:Dish;
  visibility='shown';

  feedcomment:FormGroup;

  

  formErrors={
    'author':'',
    'comment':''
  }

  validationmessage={
    'author':{
      'required':'name is required',
      'minlength':'min length of name should be atleast 2'
    },
    'comment':{
      'required':'Comment is required'
    }
  }


  

  constructor(private dishservice: DishService,
    private route: ActivatedRoute,
    private location: Location,private fb:FormBuilder,
    @Inject('BaseURL') private BaseURL) { 
      this.createform();
    }

    

    createform(){
      this.feedcomment=this.fb.group({
        author:['',[Validators.required,Validators.minLength(2),Validators.maxLength(25)]],
        rating:['5'],
        comment:['',[Validators.required]]
      })

      this.feedcomment.valueChanges.subscribe(data=>this.onvaluechange(data));
    }

    onSubmit(){
      const cc= <Comment>(this.feedcomment.value);
      cc.date=String(new Date);
      this.dish.comments.push(cc);
      this.dishcopy=this.dish;
      this.dishservice.putDish(this.dishcopy).subscribe(dish => {
        this.dish = dish; this.dishcopy = dish;
      },
      errmess => { this.dish = null; this.dishcopy = null; this.errMsg = <any>errmess; });
      this.feedcomment.reset();
      this.createform();
    }

    onvaluechange(data?){
      if(!this.feedcomment){return};
      const feed =this.feedcomment;
      for(const field in this.formErrors)
      {
        if(this.formErrors.hasOwnProperty(field))
        {
          this.formErrors[field]='';
          const control=feed.get(field);
          if(control && control.dirty && !control.valid){
            const messages=this.validationmessage[field];
            for (const key in control.errors){
              if(control.errors.hasOwnProperty(key))
              {
                this.formErrors[field]+=messages[key]+' ';
              }
            }
          }
        }
      }
    }

  ngOnInit() {
    this.dishservice.getDishIds().subscribe(dishIds => this.dishIds = dishIds,errmsg=>this.errMsg=errmsg);
    this.route.params.pipe(switchMap((params: Params) => {this.visibility='hidden'; 
          return this.dishservice.getDish(params['id']);}))
    .subscribe(dish => { this.dish = dish;this.dishcopy=dish; this.setPrevNext(dish.id);  this.visibility = 'shown'; },errmsg=>this.errMsg=errmsg);
  }

  setPrevNext(dishId: string) {
    const index = this.dishIds.indexOf(dishId);
    this.prev = this.dishIds[(this.dishIds.length + index - 1) % this.dishIds.length];
    this.next = this.dishIds[(this.dishIds.length + index + 1) % this.dishIds.length];
  }
  goBack(): void {
    this.location.back();
  }
  

}
